import { ObjectID } from './sense-object';
import { ActionUnion, emptyAction } from './index';
import { objectId, TimeStamp } from './utils';

// sense card data
export type CardID = string;

export enum CardType {
  NORMAL = 'NORMAL',
  QUESTION = 'QUESTION',
  ANSWER = 'ANSWER',
  NOTE = 'NOTE',
}

export const color = {
  [CardType.NORMAL]: 'rgba(255, 255, 255, 1)',
  [CardType.NOTE]: 'rgba(255, 255, 255, 1)',
  [CardType.QUESTION]: 'rgba(255, 236, 239, 1)',
  [CardType.ANSWER]: 'rgba(222, 255, 245, 1)'
};

export const typeToString = (type: CardType) => {
  switch (type) {
    case CardType.NORMAL:
      return 'NORMAL';
    case CardType.QUESTION:
      return 'CARD';
    case CardType.ANSWER:
      return 'ANSWER';
    case CardType.NOTE:
      return 'NOTE';
    default:
      return 'UNKNOWN';
  }
};

export const stringToType: (name: string) => CardType =
  name => CardType[name];

export interface CardData {
  id: CardID;
  createdAt: TimeStamp;
  updatedAt: TimeStamp;
  objects: { [key: string]: { id: ObjectID } };
  title: string;
  summary: string;
  saidBy: string;
  stakeholder: string;
  url: string;
  cardType: CardType;
}

export const emptyCardData: CardData = {
  id: '0',
  createdAt: 0,
  updatedAt: 0,
  objects: {},
  title: '',
  summary: '',
  saidBy: '',
  stakeholder: '',
  url: 'http://example.com',
  cardType: CardType.NORMAL
};

